import Vue from 'vue'
import App from './App.vue'
import myRouter from './router/index.js'
import store from './store/index.js'
import Element from "element-ui";
import axios from 'axios';
import "element-ui/lib/theme-chalk/index.css";
import './css/common.css'
import { mavonEditor } from 'mavon-editor';
import 'mavon-editor/dist/css/index.css'; // 
//vuex
import domStatus from './js/vuex/domStatus';//
Vue.prototype.$domStatus = domStatus;
import userInfo from './js/vuex/userInfo';//
Vue.prototype.$userInfo = userInfo;

Vue.config.productionTip = false
axios.defaults.baseURL = '/'
axios.defaults.headers.post['Content-Type'] = 'application/json'

Vue.component('mavonEditor', mavonEditor)
Vue.use(Element)
//  axios  Vue  $axios 
Vue.prototype.$axios = axios

// 
axios.interceptors.request.use(
  config => {
    //  URL 
    const path = config.url.split('/').pop()
    
    //  token 
    if (path !== 'login' && path !== 'register') {
      const token = localStorage.getItem('token')
      if (token) {
        config.headers['Authorization'] = `Bearer ${token}`
      }
    }
    
    return config
  },
  error => {
    return Promise.reject(error)
  }
)
axios.interceptors.response.use(
  response => response,
  error => {
    if (error.response && error.response.status === 401) {
      console.log('token,')
      //token
      localStorage.removeItem('token')
      store.commit('auth/CLEAR_AUTH')
      //401
      if(myRouter.currentRoute.path !== '/auth'){
        myRouter.push('/auth')
      }
      else{
        return Promise.reject(error)
      }
    }
    return Promise.reject(error)
  }
)

new Vue({
  render: h => h(App),
  router:myRouter,
  store,
}).$mount('#app')