<template>
  <div id="AvatarCropper" >
    <h1></h1>
    <div class="flex-display">
      <div class="cropper-container">
        <vue-cropper
              ref="cropper"
              :img="image"
              :outputSize="option.outputSize"
              :outputType="option.outputType"
              :info="option.info"
              :canScale="option.canScale"
              :autoCrop="option.autoCrop"
              :autoCropWidth="option.autoCropWidth"
              :autoCropHeight="option.autoCropHeight"
              :fixed="option.fixed"
              :fixedNumber="option.fixedNumber"
              :full="option.full"
              :fixedBox="option.fixedBox"
              :canMove="option.canMove"
              :canMoveBox="option.canMoveBox"
              :original="option.original"
              :centerBox="option.centerBox"
              :height="option.height"
              :infoTrue="option.infoTrue"
              :maxImgSize="option.maxImgSize"
              :enlarge="option.enlarge"
              :mode="option.mode"
              :limitMinSize="option.limitMinSize"
              @realTime="realTime"
              @imgLoad="imgLoad"
              >
          </vue-cropper>
      </div>
      <!--  -->
      <div class="cropperShow">
        <img :src="showCropper">
      </div>
    </div>
    <div class="flex-display btn-Group">
      <el-button type="primary" @click="uploadCroppedImage"></el-button>
      <el-button type="primary" @click="cancelCropper"></el-button>
    </div>
  </div>
</template>

<script>
import { VueCropper } from 'vue-cropper';
import 'cropperjs/dist/cropper.css';

export default {
  name: 'AvatarCropper',
  components: {
    VueCropper,
  },
  props: {
    image: {
      type: String,
      required: true,
    },
    limitInfo: {
      type: Object,
      required: true,
    }
  },
  data() {
    return {
      option:{           //
        outputSize: 1,       //(0.1 - 1)
        outputType: 'jpeg',  //jpeg || png || webp
        info: true,          //
        canScale: false,      //
        autoCrop: true,      //
        autoCropWidth: 400,  //
        autoCropHeight: 400, //
        limitMinSize : [null,null],
        fixed: true,         //
        fixedNumber: [1, 1], //
        full: false,         //false
        fixedBox: false,      //
        canMove: false,      //
        canMoveBox: true,    //
        original: false,     //
        centerBox: true,    //
        height: true,        //dpr 
        infoTrue: false,     //truefalse
        maxImgSize: 400,    //
        enlarge: 1,          //
        mode: 'contain'  //
      },
      realTimer : null,
      showCropper : "",
    }
  },
  methods: {
    realTime() {   
      clearTimeout(this.realTimer)
      this.realTimer = setTimeout(() => {
        this.getCroppedImage()
      }, 200)
  
    },
    imgLoad() {
      //
      if(this.limitInfo.heigh > this.limitInfo.width){
        this.option.limitMinSize[0] = this.limitInfo.width / 3;
        this.option.limitMinSize[1] = this.limitInfo.width / 3;
      }
      else{
        this.option.limitMinSize[0] = this.limitInfo.height / 3;
        this.option.limitMinSize[1] = this.limitInfo.height / 3;
      }
    },
    getCroppedImage() {
      this.$refs.cropper.getCropData(data => {
        this.showCropper = data
      })
    },
    cancelCropper(){
      this.$emit('cancelCropper')
    },
    uploadCroppedImage(){
      this.$refs.cropper.getCropData((data) => {
        this.$emit('uploadAvatar', data)
      })
    }
  }
};
</script>

<style scoped>
#AvatarCropper{
 width: 850px;
 background-color: antiquewhite;
 >h1{
  font-size: 2em;
 }
}
.cropper-container {
  width: 400px;
  height: 400px; /*  */
}
.cropperShow{
  width: 400px;
  height: 400px;
  background: #fff;
  position: relative;
  >img{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 300px;
    height: 300px;
    border-radius: 50%;
  }
}
.btn-Group{
  margin-top: 20px;
  justify-content: center;
}
</style>
