import Vue from 'vue'

export default {
  namespaced: true, // 
  state: {
    token: localStorage.getItem('token') || '', // token
    user:null, // ,
    profileUrl:null, // 
    username:null // 
  },
  getters: {
    // 
    isAuthenticated: state => !!state.token,
    // 
    user: state => state.user,
    profileUrl: state => state.profileUrl,
    username: state => state.username
  },
  actions: {
    // ,app.vuecreatedgetUserInfo,
    async login({ commit,dispatch }, credentials) {
      try {
        const response = await Vue.prototype.$axios.post('/api/user/auth/login', credentials);
        const token = response.data.token;
        //token
        localStorage.removeItem('token');
        localStorage.setItem('token', token); // token
        //fetchUser
        await dispatch('fetchUser');
        commit('SET_TOKEN', token);
        return true;
      } catch (error) {
        console.error(':', error);
        return false;
      }
    },
    
    // 
    async logout({ commit }) {
      commit('CLEAR_AUTH');
    },
    
    // 
    async fetchUser({ commit }) {
      try {
        const response = await Vue.prototype.$axios.post('/api/user/auth/getUserInfo');
        if(response.status === 200){
          commit('SET_USER', response.data.userInfo);
          commit('SET_PROFILE_URL', response.data.userBaseInfo.profileUrl);
          commit('SET_USERNAME', response.data.userBaseInfo.name);
        }
      } catch (error) {
        if(error.response && error.response.status === 401){ 
          console.log('token')
          commit('CLEAR_AUTH')
        }
      }
    }
  },
  mutations: {
    // token
    SET_TOKEN(state, token) {
      state.token = token;
    },
    // 
    SET_USER(state, user) {
      state.user = user;
    },
    // 
    SET_PROFILE_URL(state, profileUrl) {
      state.profileUrl = profileUrl;
    },
    //
    SET_USERNAME(state, username) {
      state.username = username;
    },
    // 
    CLEAR_AUTH(state) {
      state.token = '';
      state.user = {};
      localStorage.removeItem('token');
    }
  }
};