<template>
  <el-form :model="loginForm" :rules="loginRules" ref="loginForm">
    <el-form-item prop="username">
      <el-input v-model="loginForm.username" placeholder=""></el-input>
    </el-form-item>
    <el-form-item prop="password">
      <el-input v-model="loginForm.password" type="password" placeholder=""></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('loginForm')"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { mapActions } from 'vuex'  // 

export default {
  name: 'LoginForm',
  data() {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      loginRules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    ...mapActions('auth', ['login']), //  Vuex auth  login action
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // 
          this.login(this.loginForm).then(success => {
            if (success) {
              // 
              this.$message({
                message: '',
                type: 'success'
              });
              this.$router.push('/scheduleBlock/mySchedule'); // 
            } else {
              // 
              this.$message.error('');
            }
          });
        } else {
          // 
          this.$message.error('');
          return false;
        }
      });
    }
  }
};
</script>