<template>
  <el-form :model="registerForm" :rules="registerRules" ref="registerForm">
    <el-form-item prop="username">
      <el-input v-model="registerForm.username" placeholder=""></el-input>
    </el-form-item>
    <el-form-item prop="password">
      <el-input v-model="registerForm.password" type="password" placeholder=""></el-input>
    </el-form-item>
    <el-form-item prop="email">
      <el-input v-model="registerForm.email" placeholder=""></el-input>
    </el-form-item>
    <el-form-item prop="verificationCode">
      <el-input v-model="registerForm.verificationCode" placeholder="">
        <el-button slot="append" @click="sendVerificationCode" :disabled="cooldown > 0">
          {{ cooldown > 0 ? `${cooldown}` : '' }}
        </el-button>
      </el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="toRegister()"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: 'RegisterForm',
  data() {
    return {
      registerForm: {
        username: '',
        password: '',
        email: '',
        verificationCode: ''
      },
      registerRules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: 'blur' }
        ],
        verificationCode: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      cooldown: 0
    };
  },
  methods: {
    
    sendVerificationCode() {//
      // 
      this.$axios.post('api/user/auth/send-verification-code', {email:this.registerForm.email}).then(()=>{
        this.$message({
          message: ` ${this.registerForm.email}`,
          type: 'info'
        });
        this.cooldown = 60;
        const timer = setInterval(() => {
          this.cooldown--;
          if (this.cooldown <= 0) {
          clearInterval(timer);
          }
        }, 1000);
      }).catch(()=>{
        this.$message.error('');
      })
     
    },
    toRegister(){
      //
      if(this.registerForm.username === '' || this.registerForm.password === '' || this.registerForm.email === '' || this.registerForm.verificationCode === ''){
        this.$message.error('');
        return;
      }
      const {username,password,email, verificationCode:code } = this.registerForm
      this.$axios.post('api/user/auth/register',{username,password,email, code }).then(()=>{
        this.$message.success('');
        this.$emit('registerSuccess');
      }).catch(()=>{
        this.$message.error('');
      })
    }
  }
};
</script>